import Li from '@smart-react-components/core/Element/Li';
import styled from 'styled-components';
export default styled(Li)(({ nestedLevel }) => `
  ${nestedLevel
    ? `
      &::marker {
        color: transparent !important;
      }

      ${nestedLevel > 1
        ? `
          > ul {
            list-style-type: ${nestedLevel === 2 ? 'circle' : 'square'};
          }
        `
        : ''}
    `
    : ''}
`);
